@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Display&display=swap);
*{
    box-sizing: border-box;
    margin:0;
    padding:0;
}
body{
    font-family: 'Big Shoulders Display',cursive;
}
a{
    color:inherit;
    text-decoration: none;
}
main{
    margin-top: 4rem;
}

.main{
    background-color: burlywood;
}
.main > h1{
    text-align: center;
    padding:1rem;
}

footer{
    margin-top:auto;
    color: white;
    background: #5f2c82;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#49a09d), to(#5f2c82));
    background: linear-gradient(to right, #49a09d, #5f2c82); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding:1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
@media(min-width:572px){
    footer{
        flex-direction: row;
        justify-content: space-between;

    }
}

.loader:empty {
    position: absolute;
    top: calc(50% - 4em);
    left: calc(50% - 4em);
    width: 6em;
    height: 6em;
    border: 1.1em solid rgba(0, 0, 0, 0.2);
    border-left: 1.1em solid #000000;
    border-radius: 50%;
    -webkit-animation: load8 1.1s infinite linear;
            animation: load8 1.1s infinite linear;
  }
  
@-webkit-keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
@keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
header{
    width:100%;
    position: absolute;
    top:0;
    left:0;
    height:4rem;
    color: white;
    background: #5f2c82;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#49a09d), to(#5f2c82));
    background: linear-gradient(to right, #49a09d, #5f2c82); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    border-bottom:1px dotted white;
    letter-spacing: 1px;

}
header > div{
    display: flex;
    align-items: center;
    margin:0 1rem;
    height: 100%;
    font-size:2rem;
}
.header-icon{
    margin-left:auto;
    cursor: pointer;
}
.mobile-nav{
    overflow: hidden;
    height:0;
    display: flex;
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    flex-direction: column;
    justify-content: center;
    background: #000;  /* fallback for old browsers */
    
}
.mobile-nav a{
    width: 100%;
    text-align: center;
    font-size:1.5rem;
    padding:0.5rem 0;
}
.mobile-nav a:hover{
    background-color: #5f2c82;
}
.nav-shown{
    height:5.5rem;
}
.screen-nav{
    display: none;
}
@media (min-width:992px){
    .header-icon{
        display: none;
    }
    .mobile-nav{
        display: none;
    }
    .screen-nav{
        display: flex;
        width:75%;
        margin-left: auto;
        height: 100%;
        font-size:1.5rem;
    }
    .screen-nav a{
       display: block;
       line-height: 4rem;
       width:10rem;
       text-align: center;
    }
    header > div {
        width:90%;
        margin-left: auto;
    }
    .selected{
        background-color: #2f044d;
    }
    .screen-nav a:hover{
        background-color: #5f2c82;
    }
}

.banner{
    height:50rem;
/*     background: #5f2c82;  
    background: -webkit-linear-gradient(to right, #49a09d, #5f2c82);  
    background: linear-gradient(to right, #49a09d, #5f2c82);  */
    background:url(/NTF/static/media/banner-back.5989599a.jpg);
    background-attachment: fixed;
    background-size: cover;
    background-position: right;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    color:black;
    /* font-family: 'Courier New', Courier, monospace; */
}
.banner h3{
font-size:2rem;
}
.banner p{
    width:80%;
    font-size: 1.5rem;
}
.banner button{
    background-color: transparent;
    color:black;
    border-radius: 20px;
    font-size:1.2rem;
    margin-top:1.5rem;
    border:1px solid black;
    padding:0.5rem 2rem;
    cursor: pointer;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    outline:0;
}
.banner button:hover{
    background-color: #5f2c82;
    color:white;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
@media(min-width:992px){
    .banner p{
        width: 50%;
    }
    .banner{
        height:40vh;
        background-size: unset;
        background-position: unset;
    }
}
.field-box{
    color:black;
    padding:1rem;
    background-color: burlywood;
    box-shadow: 0px 0px 14px gray;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
    font-size:larger;
}
.field-box:hover{
    -webkit-transform:scale(0.95);
            transform:scale(0.95)
}

.field-box::after{
    content: '';
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    box-shadow:inset 0px 0px 100px gray;
}
.field-box h2,h3{
    margin-bottom: 0.2rem;
    text-transform: capitalize;
    z-index:5;
}
.field-box p{
    z-index: 5;
}
.field-box button{
    background-color: white;
    color:black;
    border-radius: 20px;
    font-size:1.2rem;
    margin-top:1.5rem;
    border:1px solid black;
    padding:0.5rem 2rem;
    cursor: pointer;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    outline:0;
    align-self: center;
    z-index:5;
}
.field-box button:hover{
    background-color: #5f2c82;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.medicine{
    background-image: url(/NTF/static/media/medicine-small-back.268887aa.jpg);    
}
.engineering{
    background-image: url(/NTF/static/media/engineering-small-back.3f24a8ae.jpg);    
}
.sciences{
    background-image: url(/NTF/static/media/sciences-small-back.e2f5a447.jpg);   
    background-position: top; 
}
.veterinary{
    background-image: url(/NTF/static/media/veterinary-small-back.dea9fc05.png); 
    background-position: bottom;
}
.fields-box{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(255px,1fr));
    grid-column-gap: 0.5rem;
    -webkit-column-gap: 0.5rem;
       -moz-column-gap: 0.5rem;
            column-gap: 0.5rem; 
    grid-row-gap: 1rem; 
    row-gap: 1rem;
    padding-bottom: 2rem;
}


@media(min-width:572px){
    .fields-box{
        grid-template-columns: repeat(auto-fill,minmax(500px,1fr));
    }
}
@media(min-width:992px){
    .fields-box{
        grid-template-columns: repeat(auto-fill,minmax(400px,1fr)); 
        width: 95%;
        margin: auto;
    }
}
